<template>
    <div class="app-preview">
        <div class="preview-container">
            <span class="row-tag">Container</span>
            <draggable class="drag-box" @change="change(fatherList)" :list="fatherList" :options="{group: 'people',scroll: true}">
                <div v-for="(element,index) in fatherList" :key="index">
                    <el-row :gutter="20" class="row-fluid"> 
                        <span class="row-tag">Row</span>
                        <el-col class="row-column" :span="24/element.type" v-for="(column, key) in element.childList" :key="key">
                            <span class="row-tag">Column</span>
                            <draggable class="child-drag-box" @change="changeChild(column)"  :list="column" :options="{group:{ name:'base' }}" @end="moved">
                                <div v-for="(child, chiildIndex) in column" :key="chiildIndex">
                                    <div class="child-component">
                                       <component :is="child.type" :obj="child"></component>
                                    </div>
                                </div>                            
                            </draggable>
                        </el-col>
                    </el-row>
                </div>
            </draggable>
        </div>
    </div>
</template>
<script>
import draggable from 'vuedraggable'
import deepClone from '@/utils/deepClone'
import Input from '@/components/template/Input'
import Select from '@/components/template/Select'
export default {
    components: {
        draggable,
        Input,
        Select
    },
    data() {
        return {
            fatherList: [],
            childList: []
        }
    },

    watch: {
        // fatherList: {
        //     handler(newQuestion, oldQuestion) {
        //         console.log(newQuestion)
        //         this.fatherList = JSON.parse(JSON.stringify(newQuestion))
        //     }
        // },
        // deep: true
    },
    methods: {
        change() {
           this.fatherList = deepClone(this.fatherList)
        },
        changeChild(list) {
            console.log(list)
        },
        moved(value) {
            console.log(value)
        }
    }
}
</script>

<style lang="less" scoped>
    .app-preview{
       height: calc(100% - 60px);
       overflow-y: auto; 
       width: calc(100% - 400px);
       position: fixed;
       top:60px;
       left: 200px;
       background: #fff;
       padding: 10px;
    }
    .preview-container,.drag-box{
        height: 100%;
        width: 100%;
        
    }
    .preview-container{
        padding: 30px 15px 15px;
        border: 1px solid #DDDDDD;
        border-radius: 4px;
        position: relative;
        word-wrap: break-word;
        height: auto;
        // min-height:100%;
    }
    .child-component{
        padding: 6px 0;
        font-size: 12px;
    }
</style>

